.banner {
  color: #fff;
  object-fit: contain;
  height: 450px;
  background-size: 100% 100% !important;
}
.banner_content {
  margin-left: 30px;
  padding-top: 150px;
  height: 190px;
}
.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
  margin-left: 20px;
}
.banner_description {
  font-size: 0.8rem;
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  max-width: 360px;
  height: 80px;
}
.banner_btn {
  cursor: pointer;
  color: #fff;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.4);
  padding-bottom: 0.5rem;
  outline: none;
}
.banner_btn:hover {
  color: #000;
  background-color: white;
  transition: all 0.2s;
}
.banner--fadebottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(51, 51, 51, 0.5),
    #111
  );
}
